
const SLIDER = new Swiper(".page", {
  wrapperClass: "page__wrapper",
  slideClass: "page__screen",
  direction: checkSliderOrientation(),
  // slidesPerView: 'auto',
  mousewheel: {
    sensitivity: 1,
  },
  keyboard: {
    enabled: true,
    onlyInViewport: true,
    pageUpDown: true,
  },
  speed: 800,

  pagination: {
    el: '.page__pagination',
    type: 'bullets',
    clickable: false,
    bulletClass: "page__bullet",
    bulletActiveClass: "page__bullet_active",
  },
  navigation: {
    nextEl: ".scroll-indicator-next",
    prevEl: ".scroll-indicator-prew",
  }
});

    
 
const numOfSliders = document.querySelectorAll('.page__screen').length - 1;
//  
const pointerCircles = document.querySelectorAll('.cls-3');
//  
const pointerLine = document.querySelectorAll('.cls-5');
//  
const pointerHead = document.querySelectorAll('.cls-6');

function addActiveClass(item) {
  item.forEach(function (e, i) {
    if (!e.classList.contains('active'))
      e.classList.add("active");
  });
}


window.addEventListener('load', windowLoad);
function windowLoad() {
  addActiveClass(pointerCircles);
  addActiveClass(pointerLine);
  addActiveClass(pointerHead);

}

SLIDER.on('slideChange', function () {
  //console.log(SLIDER.activeIndex);
});

SLIDER.on('beforeResize', function () {
SLIDER.changeDirection(checkSliderOrientation(), true);
});

function checkSliderOrientation(){
 //console.log(" ");
  let windowWidth = parseInt(document.documentElement.clientWidth);
  let windowHeight = parseInt(document.documentElement.clientHeight);
  let orientation;
  if (windowHeight < windowWidth){
   // console.log(` `);
   orientation = 'vertical';
   // SLIDER.direction = ;
   //  console.log(SLIDER.direction);
  }
  else {
//console.log(` `);
orientation = 'horizontal';
//console.log(SLIDER.direction);
  }
 // console.log(SLIDER.activeIndex);
 // SLIDER.updateSize();
 return orientation;
}





